<template>
  <div id="app">
    <h3>
      SPECIAL OFFER: 20% OFF BOOKS WITH WORDS IN THEM!!! LIMITED TIME ONLY!
    </h3>
    <hr />
    
    <div class="topbar">
      <img src="static/logo-horizontal.png" />
      <input type="text" name="fname" />
      <button>Search</button>
      <div class="account">
        <button>Log in</button>
        <button>Sign up</button>
      </div>
    </div>
    <hr />
    <div class="main-container">
      <router-view name="componentShop"></router-view>
      <router-view name="componentDetail"></router-view>
      <router-view name="componentSidebar"></router-view>
    </div>
  </div>
</template>

<script>
import Shop from "./components/Shop";
import Sidebar from "./components/Sidebar";
import Detail from "./components/Detail";

export default {
  name: "App",
  components: {
    "component-sidebar": Sidebar,
    "component-Shop": Shop,
    "component-detail": Detail
  }
};
</script>

<style>
.main-container {
  display: flex;
}
.account {
  align-self: center;
  margin-left: 20pt;
  align-content: center;
}
.topbar {
  display: flex;
}
input {
  margin-left: 40pt;
  flex-grow: 1;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 10px;
  margin-left: 10px;
}
h3 {
  text-align: center;
}
img {
  width: 180pt;
  height: 30pt;
}
</style>



